import React, {Component} from 'react';
import PropTypes from 'prop-types';

class Display extends Component {

	constructor() {
		super();

		this.state = {
			weatherData: null
		}
	}

	componentDidMount() {
		const id = this.props.city.id;
		const URL = `http://api.openweathermap.org/data/2.5/weather?id=${id}&appid=b1b35bba8b434a28a0be2a3e1071ae5b&units=metric`;


		fetch(URL).then(res => res.json()).then(json => {
	      this.setState({ weatherData: json });
	    });
	}

	render() {

		const {weatherData} = this.state;
		if (!weatherData) return <div>Loading</div>;
		const {weather} = weatherData;
		const iconUrl = "http://openweathermap.org/img/w/" + weather[0].icon + ".png";


		return (
	      <div>
	        <h1>
	          {weather[0].main} in {weatherData.name}
	          <img src={iconUrl} alt={weatherData.description} />
	        </h1>
	        <p>Current: {weatherData.main.temp}C</p>
	        <p>High: {weatherData.main.temp_max}C</p>
	        <p>Low: {weatherData.main.temp_min}C</p>
	        <p>Wind Speed: {weatherData.wind.speed} mi/hr</p>
	      </div>
	    );	
	}
}

Display.propTypes = {
	city: PropTypes.object
}

Display.defaultProps = {
	city: {}
}

export default Display;


