import React, {Component} from 'react';
import PropTypes from 'prop-types';
import './Button.css';

function Button({city, onSetActiveCity}) {
	
	function setActiveCity() {
		onSetActiveCity(city);
	}


	return (
		<button onClick={setActiveCity}>
            {city.name}
        </button>
	)
}

export default Button;


