import React, { Component } from 'react';
import './App.css';
import logo from './logo.svg';
import { connect } from 'react-redux'

import Display from './components/Display';
import Button from './components/Button'
import activeCity from './actions/setCity'

const CITIES = [
  { name: "Brest", id: 629634},
  { name: "Vitsyebsk", id: 620127},
  { name: "Minsk", id: 625144},
  { name: "Homyel", id: 627907},
  { name: "Mahilyow", id: 625665}
];

class App extends Component {



  render() {
    const {activeCity} = this.props;

    console.log(this.props.store);

    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">Weather in your city</h1>
          <img className="logo" src={logo} alt="weather" />
          <p>{this.props.activeCity.name}</p>
        </header>
        <main>
          <aside>
            {
              CITIES.map(city =>
                <Button 
                  key={city.id}
                  city={city}
                  onSetActiveCity={this.props.setActiveCity}
                />
              )
            }
          </aside>
          <div>
            {activeCity ? <Display key={activeCity.id} city={activeCity}/> : null }
          </div>
        </main>
      </div>
    );
  }
}

function mapStateToProps(store) {
  return {
    user: store.userInfo,
    activeCity: store.activeCity,
    store
  }
}

function mapDispatchToProps(dispatch) {
  return {
    setActiveCity: city => {
      dispatch(activeCity(city))
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);



